<template>
    <div class="header">
        <div class="container clearfix">
            <a class="header-logo-invertocat" href="/">
                <svg aria-hidden="true" class="octicon octicon-mark-github" height="32" version="1.1" viewBox="0 0 16 16" width="32">
                    <path fill-rule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"></path>
                </svg>
            </a>

            <div class="header-search scoped-search">

                <div class="form-control header-search-wrapper js-chromeless-input-container">
                    <div class="header-search-scope">Enter a Github Username</div>
                    <input  type="text" autocomplete="off" class="form-control header-search-input" placeholder="Search User Name"
                        v-model="userName"
                        v-on:keyup="keyUpHandler">
                </div>
            </div>

            <ul class="header-nav float-left" role="navigation">
                <li class="header-nav-item"><a href="https://twitter.com/_tipek" class="header-nav-link">Twitter</a> </li>
                <li class="header-nav-item"><a class="header-nav-link" href="https://github.com/tahaipek/VueJs-2-Github-Search-Profile">Github</a> </li>
            </ul>

        </div>

    </div>
</template>

<script>
    export default {
        name: 'appHeader',
        props: ['paramUserName'],
        data() {
            return {
                userName: this.paramUserName
            }
        },
        methods: {
             keyUpHandler(event) {
                // console.log(this.userName + ' - Key: ' + event.key);
                this.$emit('FetchProfile', this.userName);
            },
        }
    }
</script>


<style>
    .header .header-search {
        width: 750px;
    }
</style>


